.guarantee-list {
  padding: 35px 0 75px;
  background: url(../img/bg/features.png) no-repeat center / cover;
  &__inner {

  }
  &__row {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
  }
  &__item {
    // flex-shrink: 0;
    flex-basis: calc(33.333% - 40px);
    & + & {
      // margin-left: 60px;
    }
  }
  &__item-header {
    display: flex;
    align-items: flex-end;
    justify-content: flex-start;
    margin-bottom: 35px;
  }
  &__item-icon {
    width: 115px;
    height: 115px;
    border: 1px solid #ffffff;
    border-radius: 50%;
    padding: 25px;
    flex-shrink: 0;
    margin-right: 30px;
  }
  &__item-title {
    font-size: $fz-subtitle;
    line-height: 30px;
    font-weight: 800;
    color: #ffffff;
    white-space: nowrap;
    &::after {
      content: '';
      display: block;
      width: 60px;
      height: 5px;
      background-color: #fca455;
      background-image: linear-gradient(90.0deg, $accent-dark 0%, $accent-lite 100%);
      margin-top: 5px;
      margin-bottom: 10px;
    }
  }
  &__item-text {
    line-height: 30px;
    color: #ffffff;
  }
}

@media (min-width: 768px) and (max-width: 1200px) {
  .guarantee-list {
    &__row {
      flex-wrap: wrap;
      justify-content: center;
    }
    &__item {
      flex-basis: 40%;
      margin-bottom: 50px;
      &:nth-of-type(even) {
        margin-left: 40px;
      }
    }
  }
}
@media (min-width: 992px) and (max-width: 1199px) {}
@media (min-width: 768px) and (max-width: 991px) {}
@media (max-width: 767px) {
  .guarantee-list {
    background-position: 15% center;
    padding-bottom: 0;
    &__row {
      flex-direction: column;
      max-width: 420px;
      margin: 0 auto;
    }
    &__item {
      margin-bottom: 45px;
    }
    &__item-header {
      margin-bottom: 10px;
    }
    &__item-icon {
      width: 80px;
      height: 80px;
      padding: 15px;
    }
    &__item-title {
      font-size: $fz-default;
    }
    &__item-text {
      font-size: $fz-default;
    }
  }
}
@media (max-width: 480px) {
  .guarantee-list {
    &__item-title {
      font-size: $fz-subtitle-mob;
      line-height: 16px;
      font-weight: 800;
      color: #ffffff;
      &::after {
        width: 53px;
      }
    }
    &__item-text {
      font-size: $fz-subtitle-mob;
      line-height: 16px;
    }
  }
}
@media (max-width: 320px) {}