.header {
  $btn-color: #f9bd2f;
  padding: 25px 0;
  font-size: $fz-default;
  // position: absolute;
  // left: 0;
  // top: 0;
  // right: 0;
  background-color: #fff;
  &__inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo {
    & .logo__link {
      max-width: 130px;
    }
  }
  &__panel {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
  }
  &__email {
    // font-size: 1rem;
    line-height: 18px;
    text-decoration: underline;
    &::before {
      content: url(../img/icon/actions/email.png);
      vertical-align: middle;
      margin-right: 5px;
    }
  }
  &__btn {
    // font-size: 1rem;
    // line-height: 18px;
    white-space: nowrap;
    padding: 15px 55px;
    border: 2px solid $btn-color;
    border-radius: 10px;
    background-color: transparent;
    margin-left: 22px;
    transition: .25s;
    &:hover {
      cursor: pointer;
      background-color: $btn-color;
      // color: ;
    }
  }
  &__contacts {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-left: 70px;
  }
  &__tel {
    font-size: $fz-subtitle;
    line-height: 18px;
    font-weight: 700;
    position: relative;
    margin-left: 5px;
    margin-bottom: 10px;
    &:hover {
      text-decoration: underline;
    }
    &::before {
      content: url(../img/icon/actions/phone.png);
      vertical-align: top;
      position: absolute;
      left: -23px;
    }
  }
  &__schedule {
    line-height: 18px;
  }
}

@media (min-width: 1200px) {}
@media (max-width: 1150px) {
  .header {
    &__btn {
      padding: 15px 35px;
    }
    &__email {
      display: none;
    }
  }
}
@media (max-width: 991px) {
  .header {
    &__panel {
      flex-direction: column;
      align-items: flex-end;
    }
    &__btn, &__contacts {
      margin-left: 0;
    }
    &__btn {
      padding: 6px 24px;
    }
    &__contacts {
      order: -1;
    }
    &__schedule {
      display: none;
    }
    &__tel {
      font-size: 1.250rem;
    }
  }
}
@media (max-width: 767px) {
  .logo {
    &__text {
      display: none;
    }
  }
  .header {
    box-shadow: -3px 0px 7px 0px rgba(187,187,187,0.3);
    padding: 20px 0;
    position: relative;
    z-index: 100;
    &__logo {}
    &__tel {
      font-size: $fz-subtitle-mob;
    }
    &__btn {
      border-radius: 5px;
    }
  }
}
@media (max-width: 480px) {
  .header {
    &__btn {
      font-size: $fz-default-mob;
    }
  }
}
@media (max-width: 320px) {}