.works {
  padding: 60px 0;
  &__inner {
    
  }
  &__header {
    max-width: 760px;
    margin-bottom: 40px;
  }
}

.works-gallery {
  &__title {
    font-size: $fz-title;
    line-height: 36px;
    font-weight: 800;
    text-transform: uppercase;
    margin-bottom: 30px;
  }
  &__slider {
    overflow: hidden;
    position: relative;
  }
  &__next, &__prev {
    width: 60px;
    height: 60px;
    background: no-repeat center / cover;
    position: absolute;
    z-index: 1;
    top: calc(50% - 30px);
    cursor: pointer;
    // left: 20px;
  }
  &__prev {
    background-image: url(../img/icon/arrows/left-circle.png);
    left: 15px;
  }
  &__next {
    background-image: url(../img/icon/arrows/right-circle.png);
    right: 15px;
  }
  &__pagination {
    display: flex;
    justify-content: center;
    margin-top: 20px;
    & .swiper-pagination-bullet {
      display: block;
      margin: 0;
      padding: 0;
      width: 7px;
      height: 7px;
      background-color: #c3c3c3;
      border-radius: 50%;
      & + & {
        margin-left: 10px;
      }
    }
    & .swiper-pagination-bullet-active {
      color: $accent-secondary;
      background-color: $accent-secondary;
    }
  }
}

@media (min-width: 1200px) {}
@media (min-width: 992px) and (max-width: 1199px) {}
@media (min-width: 768px) and (max-width: 991px) {}
@media (max-width: 767px) {
  .works {
    padding: 20px 0 40px;
    &__header {
      margin-bottom: 30px;
      & br {
        display: none;
      }
    }
  }
  .works-gallery {
    &__title {
      font-size: $fz-base;
      text-align: center;
      margin-bottom: 15px;
    }
  }
}
@media (max-width: 480px) {
  .works-gallery {
    &__prev, &__next {
      width: 30px;
      height: 30px;
    }
    &__title {
      font-size: $fz-title-mob;
      text-align: center;
      margin-bottom: 15px;
    }
  }
}
@media (max-width: 320px) {}