.about {
  background: url(../img/bg/gray.jpg) no-repeat center / cover;
  padding: 30px 0 40px;
  &__inner {
    display: flex;
    align-items: flex-start;
    // justify-content: space-between;
  }
  &__title-mob {
    display: none;
    font-size: $fz-title-mob;
    font-weight: 800;
    text-transform: uppercase;
  }
  &__subtitle-mob {
    display: none;
    font-size: $fz-title-mob;
    font-weight: 800;
    text-transform: uppercase;
    margin-bottom: 40px;
  }
  &__content {
    padding-top: 60px;
    max-width: 560px;
  }
  &__content-mob {
    display: none;
    max-width: none;
    padding: 35px 15px 50px;
    background-color: #f1f1f1;
    background-image: url(../img/bg/about-mob.jpg);
    background-repeat: no-repeat;
    background-position: right center;
    background-size: contain;

  }
  &__header {
    margin-bottom: 80px;
  }
  &__person {
    margin-right: 60px;
  }
  &__list {}
}

.person {
  // $brs-inflated: 50% 15px;
  // position: relative;
  &__photo {
    position: relative;
    z-index: 1;
  }
  &__img {
    margin: 0 auto;
  }
  &__card {
    width: 390px;
    // width: 100%;
    // margin: -120px auto auto;
    margin-top: -100px;
    background-color: #f1f1f1;
    border: 8px solid #ffffff;
    // border-top-left-radius: $brs-inflated;
    // border-top-right-radius: $brs-inflated;
    // border-bottom-left-radius: $brs-inflated;
    // border-bottom-right-radius: $brs-inflated;
    @include card-inflated-top(15px);
    text-align: center;
    padding: 45px 15px 35px;
    position: relative;
    z-index: 2;
  }
  &__title {
    font-style: oblique;
    font-size: $fz-subtitle;
    margin-bottom: 20px;
  }
  &__subtitle {
    font-style: oblique;
    font-size: 1.125rem;
    // line-height: 30px;
    & > span {
      text-decoration: underline;
    }
  }
}

.features {
  &__title {
    font-size: $fz-subtitle;
    // line-height: 30px;
    font-weight: 800;
    margin-bottom: 25px;
  }
  &__list {
    padding-left: 30px;
    max-width: 500px;
  }
  &__item {
    font-size: $fz-subtitle;
    // line-height: 30px;
    margin-bottom: 40px;
    position: relative;
    &::before {
      content: '';
      display: block;
      width: 12px;
      height: 12px;
      // background-color: $accent-secondary;
      background-color: #fff;
      border-radius: 50%;
      position: absolute;
      left: -30px;
      top: 10px;
    }
  }
}

@media (min-width: 1200px) {}
@media (min-width: 992px) and (max-width: 1199px) {}
@media (max-width: 900px) {
  .about {
    &__inner {
      align-items: center;
    }
  }
}
@media (min-width: 768px) and (max-width: 991px) {}
@media (max-width: 767px) {
  .about {
    padding: 0;
    &__header {
      // display: none;
    }
    &__inner {
      flex-direction: column;
      // align-items: center;
      padding: 30px 0 25px;
    }
    &__title-mob {
      display: block;
      margin-bottom: 18px;
      font-size: $fz-base;
    }
    &__subtitle-mob {
      display: block;
      text-align: center;
      padding-left: 85px;
      font-size: $fz-base;
    }
    &__content {
      display: none;
    }
    &__content-mob {
      display: block;
      // max-width: 500px;
      padding-right: 100px;

    }
    &__person {
      margin: 0 auto;
    }
  }

  .person {
    // max-width: 200px;
    &__img {
      max-width: 190px;
    }
    &__card {
      margin-top: -30px;
      padding: 30px 40px 25px;
      border-width: 4px;
      width: 250px;
      @include card-inflated-top(8px);
    }
    &__title {
      font-size: $fz-title-mob;
      margin-bottom: 10px;
    }
    &__subtitle {
      font-size: $fz-default-mob;
    }
  }
  .features {
    &__title {
      // font-size: 14px;
      font-size: $fz-title-mob;
      // line-height: 30px;
      font-weight: 700;
      margin-bottom: 20px;
    }
    &__item {
      font-size: $fz-title-mob;
      margin-bottom: 18px;
      &::before {
        width: 8px;
        height: 8px;
        background-color: $accent-secondary;
      }
    }
  }
}
@media (max-width: 480px) {
  .about {
    &__content-mob {
      padding-right: 15px;
      // background-position: right center;
      // font-size: $fz-title-mob;
    }
    &__subtitle-mob {
      text-align: left;
      padding-left: 0;
      font-size: $fz-title-mob;
    }
    &__title-mob {
      font-size: $fz-title-mob;
    }
  }
}
@media (max-width: 320px) {}