$accent-dark: #ff4800;
$accent-lite: #fca44f;
$accent-secondary: #fc9f4a;
$fz-title: 1.625rem; // 26px
$fz-subtitle: 1.5rem; // 24px
$fz-base: 1.25rem; // 20px
$fz-default: 1rem; // 16px

$fz-subtitle-mob: 0.875rem; // 14px
$fz-title-mob: 0.875rem;  //14px
$fz-default-mob: 0.750rem; // 12px

$font-default: 'GreycliffCF', sans-serif;

html {

}
body {
  font-family: $font-default;
  font-size: $fz-base;
  font-weight: 400;
  color: #000000;
  overflow-x: hidden;
  &.lock {
    overflow-y: hidden;
  }
}
.wrapper {
  &.blured {
    filter: blur(1px);
  }
}
.container {
  max-width: 1170px;
  width: 100%;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
  &--small {
    max-width: 1000px;
  }
  &--big {
    max-width: 1470px;
  }
  &--larger {
    max-width: 1200px;
  }
}
.logo {
  display: flex;
  align-items: center;
  &__link {
    margin-right: 32px;
  }
  &__text {
    font-size: 1rem;
    line-height: 18px;
    max-width: 220px;
  }
}
.extrabold {
  font-weight: 800;
}

.btn-flare {
  background-image: linear-gradient(90.0deg, $accent-dark 0%, $accent-lite 100%);
  border: none;
  border-radius: 10px;
  font-size: 1.1875rem;
  font-weight: 700;
  color: #ffffff;
  text-align: center;
  padding: 15px 45px;
  transition: .25s;
  outline: none;
  display: block;
  &:hover, &:focus {
    cursor: pointer;
    box-shadow: 0 0 14px 2px $accent-dark;
  }
  &:active {
    transform: scale(0.95);
  }
  @include btn-flare;
}
@include flare-anim;

.text-grad {
  color: $accent-dark;
  @include bg-mask(linear-gradient(90.0deg, $accent-dark 0%, $accent-lite 100%));
  color: transparent;
}
span.bg-grad {
  display: inline-block;
  background-image: linear-gradient(90.0deg, $accent-dark 0%, $accent-lite 100%);
  color: #ffffff;
  border-radius: 10px;
  padding: 3px 5px;
}

.section-header {
  text-align: center;
  margin: 0 auto;
  &__title {
    margin: 0 auto;
    font-size: $fz-title;
    line-height: 36px;
    font-weight: 800;
    text-transform: uppercase;
    margin-bottom: 30px;
  }
  &__subtitle {
    margin: 0 auto;
    font-size: $fz-subtitle;
    line-height: 30px;
  }
}

.swiper-pagination-bullet {
	padding: 0;
	border-radius: 0;
	width: auto;
	height: auto;
	text-align: left;
	line-height: normal;
	color:#000;
	opacity: 1;
  background: none;
  outline: none;
}
.swiper-pagination-bullet-active {
	color: #000000;
	background: none;
}

@media (min-width: 1200px) {}
@media (min-width: 992px) and (max-width: 1199px) {}
@media (min-width: 768px) and (max-width: 991px) {}
@media (max-width: 767px) {
  .logo {
    &__img {
      max-width: 100px;
    }
    &__link {
      margin-right: 0;
    }
  }
  .btn-flare {
    font-size: $fz-subtitle-mob;
    font-weight: 600;
    padding: 15px 20px;
  }
  .section-header {
    &__title {
      font-size: $fz-base;
      line-height: 1.2;
      margin-bottom: 30px;
    }
    &__subtitle {
      font-size: 1.125rem;
      line-height: 1.2;
    }
  }
  span.bg-grad {
    border-radius: 5px;
  }
}
@media (max-width: 480px) {
  .btn-flare {
    font-size: 0.6875rem;
    font-weight: 600;
    padding: 10px 15px;
  }
  .section-header {
    &__title {
      font-size: $fz-title-mob;
      line-height: 16px;
      margin-bottom: 20px;
    }
    &__subtitle {
      font-size: $fz-subtitle-mob;
      line-height: 16px;
    }
  }
}
@media (max-width: 320px) {}