.request {
  padding: 35px 0;
  background: no-repeat right center / cover;
  background-image: url(../img/bg/request.png);
  // overflow-x: hidden;
  &--left {
    background-image: url(../img/bg/request-left.png);
    background-size: auto 80%;
    background-position: 0 bottom;
  }
  &__inner {
    position: relative;
  }
  &__header {
    margin-bottom: 80px;
    & > h2 {
      max-width: 550px;
    }
  }
  &__img {
    position: absolute;
    z-index: 2;
    left: -11%;
    top: 25%;
    // height: 100%;
    width: 66%;
    &--right {
      left: 41%;
      top: 33%;
    }
  }
  &__text {
    position: absolute;
    top: -2%;
    left: 36%;
    font-style: oblique;
    font-size: $fz-default;
    line-height: 18px;
    max-width: 200px;
    &::after {
      content: '';
      display: block;
      width: 75px;
      height: 25px;
      background: no-repeat center / cover;
      background-image: url(../img/icon/arrows/short.png);
      position: absolute;
      left: 87%;
      top: 10%;
    }
    & .undline {
      text-decoration: underline;
    }
  }
  &__form {
    max-width: 585px;
    margin: 0 0 0 auto;
    &--left {
      margin: 0 auto 0 0;
    }
  }
  &__img {
    // display: none;
  }
}

.request-form {
  padding: 90px 0px 60px 0px;
  background-image: url(../img/content/thermal-right.png);
  background-repeat: no-repeat;
  background-size: auto;
  background-position: right 40%;
  background-color: #f1f1f1;
  border: 8px solid #ffffff;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;

  @include card-inflated-top(40px);

  position: relative;
  &--left-hand {
    background-image: url(../img/content/thermal-left.png);
    background-position: left 40%;
    border: 2px solid #fc9743 !important;
    background-color: #fff;
  }
  &--phone {
    background-image: url(../img/content/phone-right.png);
  }
  &--cup {
    background-image: url(../img/content/cup-right.png);
  }
  &__img {
    width: 120%;
    margin-left: -15%;
    position: relative;
    display: none;
  }
  &__text {
    @extend .request__text;
    text-align: left;
  }
  & > form {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
  }
  &__title {
    font-size: $fz-title;
    line-height: 36px;
    font-weight: 800;
    text-transform: uppercase;
    margin-bottom: 40px;
    text-align: center;
  }
  &__subtitle {
    line-height: 30px;
    font-weight: 500;
    margin-bottom: 50px;
    & > span {
      font-weight: 700;
      text-transform: uppercase;
      color: #db9b01;
    }
  }
  &__input-wrapper {
    margin-bottom: 15px;
    max-width: 370px;
    width: 100%;
    position: relative;
    &.user-icon::before, &.phone-icon::before, &.calendar-icon::before {
      opacity: .25;
      display: block;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      left: 35px;
    }
    &.user-icon::before {
      content: url(../img/icon/actions/user-black.png);
    }
    &.phone-icon::before {
      content: url(../img/icon/actions/phone-black.png);
    }
    &.calendar-icon::before {
      content: url(../img/icon/actions/calendar-black.png);
    }
  }
  &--left-hand &__input {
    background-color: #f7f7f7;
  }
  &__input {
    background-color: #ffffff;
    border-radius: 10px;
    border: none;
    width: 100%;
    padding: 30px 30px 30px 100px;
    outline: none;
    transition: box-shadow .25s;
    &:focus {
      box-shadow: 0 0 12px 2px rgba(0, 0, 0, 0.6);
    }
    &::placeholder {
      color: rgba(0, 0, 0, 0.25);
      font-size: 1.125rem;
      line-height: 48px;
      font-weight: 500;
    }
  }
  &__btn {
    margin-top: 45px;
    max-width: 385px;
    font-size: 1.375rem;
    line-height: 27px;
    margin-bottom: 20px;
  }
  &__policy {
    font-size: 0.75rem;
    line-height: 12px;
    font-weight: 300;
    color: #a2a2a2;
    max-width: 250px;
  }
  &__footer {
    font-style: oblique;
    font-size: 1.125rem;
    line-height: 24px;
    max-width: 275px;
    margin-top: 50px;
  }
}

@media (min-width: 1200px) {}
@media (min-width: 900px) and (max-width: 1199px) {
  .request {
    &--left {
      background-size: auto 70%;
      background-position: -30% bottom;
    }
    &__form {
      max-width: 500px;
    }
    &__img {
      max-width: 50%;
      left: -3%;
      top: 15%;
      &--right {
        // max-width: 60%;
        left: 50%;
        top: 38%;
      }
    }
    &__text {
      top: -6%;
      left: 20%;
      &::after {
        width: 55px;
        height: 20px;
      }
    }
  }
  .request-form {
    padding: 45px 15px 40px;
    &__title {
      font-size: 1.375rem;
      line-height: 20px;
    }
    &__subtitle {
      font-size: $fz-default;
      line-height: 18px;
      margin-bottom: 25px;
      max-width: 85%;
    }
    &__input {
      padding: 20px 20px 20px 30%;
      &::placeholder {
        font-size: $fz-default;
      }
    }
    &__btn {
      margin-top: 20px;
      // margin-bottom: 10px;
      font-size: $fz-default;
      line-height: 18px;
      padding: 16px 30px;
    }
    &__footer {
      font-size: 1rem;
      line-height: 20px;
      max-width: 255px;
      margin-top: 30px;
    }
  }
}
@media (min-width: 768px) and (max-width: 991px) {}
@media (max-width: 899px) {
  .request {
    background-position: center;
    padding: 22px 0;
    &__img {
      display: none;
    }
    &--left {
      background-position: right bottom;
    }
  }
  .request-form {
    margin: 0 auto;
    border: 4px solid #ffffff;
    // background-image: none;
    background-position: right 94%;
    background-size: 50px;
    padding: 45px 15px 40px;

    // @include card-inflated-top(35px);
    &--left-hand {
      background-image: url(../img/content/thermal-right.png);
    }
    &__img {
      display: block;
      margin-bottom: 20px;
      width: 85%;
    }
    &__text { 
      // font-size: 0.625rem;
      font-size: 1rem;
      // line-height: 11px;
      line-height: 18px;
      font-weight: 300;
      color: $accent-lite;
      max-width: 200px;
      left: auto;
      right: 45%;
      top: -3%;
      &::after {
        width: 50px;
        height: 17px;
      }
    }
    &__title {
      // font-size: $fz-title-mob;
      font-size: $fz-subtitle;
      // line-height: 18px;
      line-height: 28px;
      padding: 0 7px 0;
    }
    &__subtitle {
      // font-size: $fz-default-mob;
      font-size: $fz-default;
      max-width: 85%;   //
      // line-height: 14px;
      line-height: 18px;
      margin-bottom: 25px;
    }
    & > form {}
    &__input-wrapper {
      margin-bottom: 10px;
      &.user-icon::before, &.phone-icon::before, &.calendar-icon::before {
        transform: scale(0.7) translateY(-50%);
      }
    }
    &__input {
      // padding: 15px 20px 15px 30%;
      padding: 20px 25px 20px 30%;
      font-size: $fz-default;
      &::placeholder {
        // font-size: $fz-default-mob;
        font-size: $fz-default;
        line-height: 1.2;
      }
    }
    &__btn {
      margin-top: 10px;
      margin-bottom: 10px;
      // font-size: 0.6875rem;
      font-size: $fz-default;
      // line-height: 13px;
      line-height: 16px;
      // padding: 12px 28px;
      padding: 15px 30px;
    }
    &__policy {
      // font-size: 0.375rem;
      // max-width: 125px;
      // line-height: 6px;
    }
    &__footer {
      display: none;
    }
  }
}
@media (max-width: 480px) {
  .request-form {
    @include card-inflated-top(20px);
    & > form {
      max-width: 320px;
      // width: 80%;
    }
    &__img {
      width: 120%;
    }
    &__text {
      font-size: 0.625rem;
      line-height: 11px;
      max-width: 135px;
      left: auto;
      right: 38%;
      top: -3%;
      &::after {
        width: 30px;
        height: 10px;
      }
    }
    &__title {
      font-size: $fz-title-mob;
      line-height: 18px;
    }
    &__subtitle {
      font-size: $fz-default-mob;
      max-width: none;
      line-height: 14px;
    }
    &__input {
      padding: 15px 20px 15px 35%;
      font-size: $fz-default-mob;
      &::placeholder {
        font-size: $fz-default-mob;
      }
    }
    &__btn {
      font-size: 0.6875rem;
      line-height: 13px;
      padding: 12px 30px;
      max-width: 255px;
    }
    &__policy {
      font-size: 0.375rem;
      max-width: 125px;
      line-height: 1.2;
    }
  }
}
@media (max-width: 320px) {}