.top {
  background: url(../img/bg/top.jpg) no-repeat center top / cover;
  padding: 60px 0 160px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  &__inner {
    height: 100%;
  }
  &__content {
    max-width: 700px;
  }
  &__title {
    font-size: 2.375rem;
    line-height: 62px;
    font-weight: 800;
    text-transform: uppercase;
    margin-bottom: 40px;
    position: relative;

  }
  &__list {}
  &__item {
    font-size: $fz-subtitle;
    line-height: 24px;
    font-weight: 500;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    margin-bottom: 55px;
    position: relative;
    padding-left: 80px;
    &::before {
      content: '';
      position: absolute;
      left: 0;
      display: block;
      width: 65px;
      height: 82px;
      background: no-repeat right center / contain;
    }
    &:nth-of-type(1)::before {
      background-image: url(../img/icon/markers/1.png);
    }
    &:nth-of-type(2)::before {
      background-image: url(../img/icon/markers/2.png);
    }
    &:nth-of-type(3)::before {
      background-image: url(../img/icon/markers/3.png);
    }
  }
  &__btn {
    max-width: 320px;
  }
}

.top-list-mobile {
  display: none;
  padding: 40px 30px 40px 65px;

  &__item {
    font-size: $fz-subtitle-mob;
    line-height: 16px;
    font-weight: 400;
    // text-align: center;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    position: relative;
    padding-left: 80px;
    & br {
      display: none;
    }
    & + & {
      margin-top: 65px;
    }
    &::before {
      content: '';
      position: absolute;
      left: 0;
      display: block;
      width: 70px;
      height: 90px;
      background: no-repeat right center / contain;
    }
    &:nth-of-type(1)::before {
      background-image: url(../img/icon/markers/1.png);
    }
    &:nth-of-type(2)::before {
      background-image: url(../img/icon/markers/2.png);
    }
    &:nth-of-type(3)::before {
      background-image: url(../img/icon/markers/3.png);
    }
  }
}

@media (min-width: 1200px) {}
@media (min-width: 992px) and (max-width: 1199px) {}
@media (min-width: 768px) and (max-width: 991px) {}
@media (max-width: 767px) {
  .top-list-mobile {
    display: block;
  }
  .top {
    padding: 35px 0px 120px;
    background-image: 
      linear-gradient(180deg, rgb(255, 255, 255) 30%, rgba(255, 255, 255, 0) 100%),
      url(../img/bg/top.jpg);
    background-position: center top, center top;
    background-size: 100% 40%, cover;
    background-repeat: no-repeat, no-repeat;
    &__list {
      display: none;
    }
    &__title {
      // font-size: 0.9375rem;
      font-size: $fz-subtitle;
      max-width: 75%;
      margin: 0 auto;
      line-height: 1.2;
      text-align: center;
      text-align-last: center;
      margin-bottom: 35px;
      z-index: 2;
      &::before {
        content: '';
        width: 80%;
        height: 0px;
        position: absolute;
        z-index: -1;
        left: 7%;
        top: 50%;
        box-shadow: 0 0 40px 30px #ffffff;
      }
    }
    &__btn {
      margin: 0 auto;
      max-width: 280px;
    }
  }
}
@media (max-width: 480px) {
  .top {
    &__title {
      font-size: 0.9375rem;
      max-width: none;
    }
    &__btn {
      margin: 0 auto;
      max-width: 220px;
    }
  }
  .top-list-mobile {
    &__item {
      & br {
        display: inline;
      }
    }
  }
}
@media (max-width: 320px) {}