*, *::before, *::after {
  box-sizing: border-box;
}

ul, ol {
  padding: 0;
}

body, h1, h2, h3, h4, p, ul, ol, li, figure, figcaption, blockquote, dl, dd {
  margin: 0;
}

body {
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  line-height: normal;
}

ul, ol {
  list-style: none;
}

a {
  text-decoration: inherit;
  color: inherit;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

img {
  max-width: 100%;
  display: block;
}

input, button, textarea, select {
  font: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}

body {
  font-family: "GreycliffCF", sans-serif;
  font-size: 1.25rem;
  font-weight: 400;
  color: #000000;
  overflow-x: hidden;
}

body.lock {
  overflow-y: hidden;
}

.wrapper.blured {
  filter: blur(1px);
}

.container {
  max-width: 1170px;
  width: 100%;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

.container--small {
  max-width: 1000px;
}

.container--big {
  max-width: 1470px;
}

.container--larger {
  max-width: 1200px;
}

.logo {
  display: flex;
  align-items: center;
}

.logo__link {
  margin-right: 32px;
}

.logo__text {
  font-size: 1rem;
  line-height: 18px;
  max-width: 220px;
}

.extrabold {
  font-weight: 800;
}

.btn-flare {
  background-image: linear-gradient(90deg, #ff4800 0%, #fca44f 100%);
  border: none;
  border-radius: 10px;
  font-size: 1.1875rem;
  font-weight: 700;
  color: #ffffff;
  text-align: center;
  padding: 15px 45px;
  transition: .25s;
  outline: none;
  display: block;
  position: relative;
  overflow: hidden;
}

.btn-flare:hover, .btn-flare:focus {
  cursor: pointer;
  box-shadow: 0 0 14px 2px #ff4800;
}

.btn-flare:active {
  transform: scale(0.95);
}

.btn-flare::after {
  content: '';
  background-image: linear-gradient(0.25turn, rgba(224, 224, 224, 0.4), rgba(224, 224, 224, 0.8), rgba(224, 224, 224, 0.4));
  display: block;
  height: 100%;
  width: 80px;
  position: absolute;
  top: 0;
  left: -200px;
  transform: translateX(0) skew(-30deg);
  animation: btnFlare 3s ease infinite;
}

@keyframes btnFlare {
  0% {
    transform: translateX(0) skew(-30deg);
  }
  20% {
    transform: translateX(0) skew(-30deg);
  }
  80% {
    transform: translateX(900px) skew(-30deg);
  }
  100% {
    transform: translateX(900px) skew(-30deg);
  }
}

.text-grad {
  color: #ff4800;
  color: transparent;
}

@supports (background-clip: text) {
  .text-grad {
    background-image: linear-gradient(90deg, #ff4800 0%, #fca44f 100%);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    color: transparent;
    background-clip: text;
  }
}

span.bg-grad {
  display: inline-block;
  background-image: linear-gradient(90deg, #ff4800 0%, #fca44f 100%);
  color: #ffffff;
  border-radius: 10px;
  padding: 3px 5px;
}

.section-header {
  text-align: center;
  margin: 0 auto;
}

.section-header__title {
  margin: 0 auto;
  font-size: 1.625rem;
  line-height: 36px;
  font-weight: 800;
  text-transform: uppercase;
  margin-bottom: 30px;
}

.section-header__subtitle {
  margin: 0 auto;
  font-size: 1.5rem;
  line-height: 30px;
}

.swiper-pagination-bullet {
  padding: 0;
  border-radius: 0;
  width: auto;
  height: auto;
  text-align: left;
  line-height: normal;
  color: #000;
  opacity: 1;
  background: none;
  outline: none;
}

.swiper-pagination-bullet-active {
  color: #000000;
  background: none;
}

@media (max-width: 767px) {
  .logo__img {
    max-width: 100px;
  }
  .logo__link {
    margin-right: 0;
  }
  .btn-flare {
    font-size: 0.875rem;
    font-weight: 600;
    padding: 15px 20px;
  }
  .section-header__title {
    font-size: 1.25rem;
    line-height: 1.2;
    margin-bottom: 30px;
  }
  .section-header__subtitle {
    font-size: 1.125rem;
    line-height: 1.2;
  }
  span.bg-grad {
    border-radius: 5px;
  }
}

@media (max-width: 480px) {
  .btn-flare {
    font-size: 0.6875rem;
    font-weight: 600;
    padding: 10px 15px;
  }
  .section-header__title {
    font-size: 0.875rem;
    line-height: 16px;
    margin-bottom: 20px;
  }
  .section-header__subtitle {
    font-size: 0.875rem;
    line-height: 16px;
  }
}

.hidden {
  opacity: 0;
}

.fi {
  opacity: 0;
}

.fi.active {
  animation: fadeIn 1s forwards;
}

.ffl {
  opacity: 0;
}

.ffl.active {
  animation: fadeFromLeft .75s forwards;
}

.ffr {
  opacity: 0;
}

.ffr.active {
  animation: fadeFromRight .75s forwards;
}

.ffd {
  opacity: 0;
}

.ffd.active {
  animation: fadeFromDown 1s ease-out forwards;
}

.zio.active {
  animation: zoomInOut 1s forwards;
}

span.zio {
  display: inline-block;
}

.dribble {
  animation: dribble 5s infinite forwards;
}

span.dribble {
  display: inline-block;
}

@keyframes fadeFromLeft {
  0% {
    transform: translateX(-500px);
    opacity: 0;
  }
  35% {
    opacity: 1;
  }
  100% {
    transform: translateX(0px);
    opacity: 1;
  }
}

@keyframes fadeFromRight {
  0% {
    transform: translateX(500px);
    opacity: 0;
  }
  35% {
    opacity: 1;
  }
  100% {
    transform: translateX(0px);
    opacity: 1;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes zoomInOut {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes fadeFromDown {
  0% {
    transform: translateY(200px);
    opacity: 0;
  }
  35% {
    opacity: 1;
  }
  100% {
    transform: translateY(0px);
    opacity: 1;
  }
}

@keyframes dribble {
  0% {
    transform: scale(1) rotate(0deg);
  }
  60% {
    transform: scale(1) rotate(0deg);
  }
  65% {
    transform: scale(1.1) rotate(0deg);
  }
  68% {
    transform: scale(1.1) rotate(20deg);
  }
  71% {
    transform: scale(1.1) rotate(-20deg);
  }
  74% {
    transform: scale(1.1) rotate(20deg);
  }
  77% {
    transform: scale(1.1) rotate(-20deg);
  }
  80% {
    transform: scale(1.1) rotate(0deg);
  }
  83% {
    transform: scale(1) rotate(0deg);
  }
}

