.top {
  background: url(../img/bg/top.jpg) no-repeat center top/cover;
  padding: 60px 0 160px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}

.top__inner {
  height: 100%;
}

.top__content {
  max-width: 700px;
}

.top__title {
  font-size: 2.375rem;
  line-height: 62px;
  font-weight: 800;
  text-transform: uppercase;
  margin-bottom: 40px;
  position: relative;
}

.top__item {
  font-size: 1.5rem;
  line-height: 24px;
  font-weight: 500;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-bottom: 55px;
  position: relative;
  padding-left: 80px;
}

.top__item::before {
  content: '';
  position: absolute;
  left: 0;
  display: block;
  width: 65px;
  height: 82px;
  background: no-repeat right center / contain;
}

.top__item:nth-of-type(1)::before {
  background-image: url(../img/icon/markers/1.png);
}

.top__item:nth-of-type(2)::before {
  background-image: url(../img/icon/markers/2.png);
}

.top__item:nth-of-type(3)::before {
  background-image: url(../img/icon/markers/3.png);
}

.top__btn {
  max-width: 320px;
}

.top-list-mobile {
  display: none;
  padding: 40px 30px 40px 65px;
}

.top-list-mobile__item {
  font-size: 0.875rem;
  line-height: 16px;
  font-weight: 400;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  position: relative;
  padding-left: 80px;
}

.top-list-mobile__item br {
  display: none;
}

.top-list-mobile__item + .top-list-mobile__item {
  margin-top: 65px;
}

.top-list-mobile__item::before {
  content: '';
  position: absolute;
  left: 0;
  display: block;
  width: 70px;
  height: 90px;
  background: no-repeat right center / contain;
}

.top-list-mobile__item:nth-of-type(1)::before {
  background-image: url(../img/icon/markers/1.png);
}

.top-list-mobile__item:nth-of-type(2)::before {
  background-image: url(../img/icon/markers/2.png);
}

.top-list-mobile__item:nth-of-type(3)::before {
  background-image: url(../img/icon/markers/3.png);
}

@media (max-width: 767px) {
  .top-list-mobile {
    display: block;
  }
  .top {
    padding: 35px 0px 120px;
    background-image: linear-gradient(180deg, white 30%, rgba(255, 255, 255, 0) 100%), url(../img/bg/top.jpg);
    background-position: center top, center top;
