.materials {
  // $brs-inflated: 50% 35px;
  padding: 50px 0;
  &__inner {

  }
  &__card {
    background-color: #fff;
    padding: 50px 100px 35px;
    @include card-inflated-top(35px);
    // border-top-left-radius: $brs-inflated;
    // border-top-right-radius: $brs-inflated;
    // border-bottom-left-radius: $brs-inflated;
    // border-bottom-right-radius: $brs-inflated;
    box-shadow: 0px 17px 35px 0px rgba(193,187,180,0.5);
  }
  &__header {
    max-width: 520px;
    margin-bottom: 50px;
  }
  &__content {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin-bottom: 40px;
  }
  &__slider {
    overflow: hidden;
    margin: 0 0 0 auto;
    width: 50%;
  }
  &__pagination {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    position: relative;
    padding-left: 30px;
    padding-right: 30px;
    &::before {
      content: '';
      display: block;
      width: 2px;
      height: 100%;
      background-color: $accent-secondary;
      position: absolute;
      left: 0;
      top: 0;
      bottom: 0;
    }
  }
  &__pagination-item {
    margin-bottom: 8px;
    color: #343b47;
    white-space: nowrap;
    &::before {
      content: attr(data-number);
      display: inline-block;
      margin-right: 20px;
      font-size: 1.125rem;
      font-weight: 800;
      text-transform: uppercase;
      padding: 6px;
      transition: .15s;
      border-radius: 50%;
    }
    &:hover {
      cursor: pointer;
      &::before {
        color: #fff;
        background-color: $accent-secondary;
      }
    }
    &.swiper-pagination-bullet-active::before {
      color: #fff;
      background-color: $accent-secondary;
    }
  }
  &__arrows {
    display: flex;
    justify-content: center;
  }
  &__prev, &__next {
    width: 17px;
    height: 31px;
    background: no-repeat center / cover;
    cursor: pointer;
  }
  &__prev {
    background-image: url(../img/icon/arrows/left.png);
  }
  &__next {
    margin-left: 130px;
    background-image: url(../img/icon/arrows/right.png);
  }

}

@media (min-width: 1200px) {}
@media (min-width: 992px) and (max-width: 1199px) {}
@media (max-width: 991px) {
  .materials {
    &__card {
      max-width: 750px;
      margin: 0 auto;
    }
    &__content {
      flex-direction: column;
      align-items: center;
    }
    &__slider {
      order: -1;
      margin: 0 auto;
      width: 80%;
    }
  }
}
@media (max-width: 767px) {
  .materials {
    padding: 0 0 20px;
    &__inner {
      margin: 0 -15px;
    }
    &__header {
      margin-bottom: 40px;
    }
    &__card {
      background-color: #fff;
      padding: 40px 20px;
      @include card-inflated-top(10px);
    }
    &__pagination {
      padding-left: 20px;
      align-self: flex-start;
      &::before {
        // margin-right: 20px;
      }
    }
    &__pagination-item {
      font-size: $fz-base;
      margin-bottom: 0px;
      line-height: 20px;
      &::before {
        font-size: $fz-base;
        padding: 4px;
        margin-right: 10px;
      }
    }
    &__prev, &__next {
      width: 15px;
      height: 30px;
    }
  }
}
@media (max-width: 480px) {
  .materials {
    &__card {
      padding: 25px 15px;
    }
    &__slider {
      width: 100%;
    }
    &__pagination-item {
      font-size: $fz-subtitle-mob;
      &::before {
        font-size: $fz-subtitle-mob;
      }
    }
    &__next {
      margin-left: 55px;
    }
  }
}
@media (max-width: 320px) {}