.footer {
  &__inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 40px 95px;
    position: relative;
  }
  &__copy {
    font-size: $fz-default;
    font-weight: 300;
  }
  &__email {
    display: none;
    font-size: $fz-default-mob;
    text-decoration: underline;
    &::before {
      content: url(../img/icon/actions/email.png);
      display: inline-block;
      vertical-align: middle;
      margin-right: 3px;
      transform: scale(0.8);
    }
  }
  &__logo {
    // margin: 0 auto;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }
  &__links {
    display: flex;
    align-items: center;
  }
  &__link {
    font-size: $fz-default;
    text-decoration: underline;
    & + & {
      margin-left: 60px;
    }
  }
}

@media (min-width: 1200px) {}
@media (min-width: 850px) and (max-width: 1199px) {
  .footer {
    &__links {
      flex-direction: column;
      align-items: flex-end;
    }
    &__link {
      & + & {
        margin-left: 0;
        margin-right: 0px;
        margin-top: 15px;
      }
    }
  }
}

@media (max-width: 849px) {
  .footer {
    .logo {
      position: static;
      transform: none;
      order: 1;
      margin-bottom: 15px;
    }
    &__inner {
      padding: 25px 0 20px;
      flex-direction: column;
    }
    &__links {
      display: flex;
      align-items: center;
      flex-direction: column;
      order: 3;
      margin-bottom: 20px;
    }
    &__copy {
      order: 4;
    }
    &__link {
      // font-size: $fz-default-mob;
      & + & {
        margin-left: 0px;
        // margin-top: 10px;
      }
    }
  }
}
