.price {
  // $brs-inflated: 80% 50px;
  padding: 35px 0 40px;
  &__inner {

  }
  &__card {
    background-color: #fff;
    padding: 50px 100px 35px;
    @include card-inflated-top(50px);
    // border-top-left-radius: $brs-inflated;
    // border-top-right-radius: $brs-inflated;
    // border-bottom-left-radius: $brs-inflated;
    // border-bottom-right-radius: $brs-inflated;
    box-shadow: 0px 11px 35px 0px rgba(193,193,193,0.5);
  }
  &__header {}
  &__list {}
}

.price-list {
  // --rad: 45% 10px;
  $brs-inflated: 50% 10px;
  margin: 0 auto;
  max-width: 880px;
  display: flex;
  flex-direction: column;
  // align-items: center;
  &__item {
    min-height: 110px;
    padding: 25px 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border: 1px solid transparent;
    transition: .15s;
    box-shadow: 0px 7px 20px 0px rgba(255,207,163,0.2);

    border-top-left-radius: $brs-inflated;
    border-top-right-radius: $brs-inflated;
    border-bottom-left-radius: $brs-inflated;
    border-bottom-right-radius: $brs-inflated;
    &:hover {
      cursor: pointer;
      border: 1px solid $accent-secondary;
      // box-shadow: 0px 7px 20px 0px rgba(255,207,163,0.2);
    }
  }
  &__text, &__value {
    font-size: $fz-subtitle;
    line-height: 30px;
    // flex-shrink: 0;
    flex-grow: 0;
  }
  &__text {
    padding-right: 30px;
    flex-basis: 75%;
  }
  &__value {
    flex-basis: 25%;
    // max-width: 180px;
    // padding-right: 30px;
    // padding-left: 60px;
    // width: 100%;
    white-space: nowrap;
    text-align: center;
    position: relative;
    &::before {
      content: '';
      display: block;
      position: absolute;
      top: calc(50% - 37px);
      left: 0;
      width: 1px;
      height: 75px;
      background-color: $accent-secondary;
    }
  }
}

@media (min-width: 1200px) {}
@media (min-width: 992px) and (max-width: 1199px) {}
@media (min-width: 768px) and (max-width: 991px) {
  .price-list {
    &__item {
      min-height: 125px;
    }
    &__text {
      font-size: $fz-default;
      line-height: 20px;
    }
  }
}
@media (max-width: 767px) {
  .price {
    padding: 15px 0 35px;
    &__inner {
      margin: -15px 0;
    }
    &__card {
      padding: 40px 15px 60px;
      box-shadow: 0px 0px 30px 0px rgba(186,186,186,0.4);
      @include card-inflated-top(30px);
    }
  }

  .price-list {
    align-items: center;
    &__item {
      padding: 40px 10px 40px 20px;
      // min-height: 180px;
      min-height: 140px;
      max-width: 600px;
      width: 100%;
    }
    &__text, &__value {
      font-size: $fz-subtitle-mob;
      line-height: 16px;
      // font-weight: 400;
    }
    &__text {
      font-size: $fz-subtitle-mob;
      line-height: 16px;
      padding-right: 15px;
      flex-basis: 65%;
    }
    &__value {
      flex-basis: 35%;
    }
  }
}
@media (max-width: 480px) {
  .price-list {
    &__item {
      padding: 30px 10px 30px 15px;
      min-height: 180px;
    }
  }
}
@media (max-width: 320px) {}