.terms {
  // $brs-inflated: 50% 25px;
  // --rad: 80% 20px;
  padding: 65px 0 75px;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
  }
  &__card {
    max-width: 415px;
    width: 100%;
    border: 8px solid #f8f8f8;
    @include card-inflated-top(25px);

    // border-top-left-radius: $brs-inflated;
    // border-top-right-radius: $brs-inflated;
    // border-bottom-left-radius: $brs-inflated;
    // border-bottom-right-radius: $brs-inflated;

    padding: 45px 30px 40px;
    text-align: center;
  }
  &__title {
    font-size: 3.125rem;
    font-weight: 800;
    text-transform: uppercase;
    margin-bottom: 35px;
  }
  &__text {
    line-height: 30px;
  }
}

@media (min-width: 1200px) {}
@media (min-width: 992px) and (max-width: 1199px) {}
@media (min-width: 768px) and (max-width: 991px) {
  .terms {
    &__card {
      max-width: 350px;
      padding: 30px 15px;

    }
  }
}
@media (max-width: 767px) {
  .terms {
    padding: 40px 0;
    &__inner {
      justify-content: space-around;
    }
    &__card {
      max-width: 250px;
      padding: 30px 10px;
      border-width: 4px;
      @include card-inflated-top(15px);
      & + & {
        margin-left: 20px;
      }
    }
    &__title {
      // font-size: $fz-subtitle;

      font-size: 1.750rem;
    }
    &__text {
      font-size: $fz-default-mob;
      line-height: 14px;
    }
  }
}
@media (max-width: 480px) {
  .terms {
    &__card {
      max-width: 180px;
      @include card-inflated-top(10px);
      & + & {
        margin-left: 5px;
      }
    }
  }
}
@media (max-width: 320px) {}