.counter {
  // $brs-inflated: 60% 13px;
  background-color: #ededed;
  padding: 55px 0 65px;
  &__inner {}
  &__header {
    margin-bottom: 20px;
  }
  &__card {
    // border-top-left-radius: $brs-inflated;
    // border-top-right-radius: $brs-inflated;
    // border-bottom-left-radius: $brs-inflated;
    // border-bottom-right-radius: $brs-inflated;
    @include card-inflated-top(13px);

    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #f8f8f8;
    padding: 65px 45px 70px 80px;
    border: 8px solid #ffffff;

    position: relative;
    &::before {
      content: '';
      display: block;
      position: absolute;
      // top: calc(50% - 65px);
      top: 20%;
      // left: -30px;
      left: 50px;
      width: 2px;
      height: 60%;
      background-color: #fcb473;
    }
  }
  &__text {
    line-height: 30px;
    max-width: 460px;
    position: relative;
    padding-right: 30px;
  }
  &__amount {
    font-size: 3.125rem;
    font-weight: 800;
    text-transform: uppercase;
    white-space: nowrap;
  }
}

@media (min-width: 1200px) {}
@media (min-width: 992px) and (max-width: 1199px) {}
@media (min-width: 768px) and (max-width: 991px) {
  .counter {
    &__amount {
      font-size: 2.5rem;
    }
  }
}
@media (max-width: 767px) {
  .counter {
    padding: 40px 0 35px;
    &__header {
      margin-bottom: 35px;
      max-width: 460px;
    }
    &__card {
      flex-direction: column;
      align-items: flex-start;
      max-width: 550px;
      margin: 0 auto;
      padding: 35px 10px 35px 35px;
      @include card-inflated-top(8px);
      &::before {
        left: 20px;
      }
    }
    &__text {
      font-size: $fz-subtitle-mob;
      line-height: 20px;
      margin-bottom: 10px;
    }
    &__amount {
      font-size: 1.875rem;
    }
  }
}
@media (max-width: 480px) {
  .counter {
    &__header {
      max-width: 320px;
    }
    &__text {
      font-size: $fz-default-mob;
      line-height: 16px;
    }
  }
}
@media (max-width: 320px) {}