.popup {
  position: fixed;
  z-index: 100;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow-y: scroll;
  background-color: rgba(204, 204, 204, 0.5);
  &__overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    // background-color: rgba(204, 204, 204, 0.5);
  }
  &__form {
    margin: 0 auto;
    max-width: 585px;
    position: relative;
    @media (max-width: 1199px) {
      max-width: 500px;
    }
    @media (max-width: 900px) {
      max-width: 450px;
    }
    @media (max-width: 480px) {
      max-width: 350px;
    }
  }
}