.contacts {
  // background: url(../img/bg/map.jpg) no-repeat right center / cover;
  // background: no-repeat left center / cover;
  // background-image: 
  //   linear-gradient(90.0deg, #f7f7f7 35%, transparent 100%);
    // url(../img/bg/map.jpg);
  // background-repeat: no-repeat, no-repeat;
  // background-position: left center, right center;
  // background-size: cover, contain;
  padding: 80px 0 90px;
  position: relative;
  // z-index: 1;
  &::before {
    content: '';
    display: block;
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    z-index: 2;
    background: no-repeat left center / cover;
    background-image: 
      linear-gradient(90.0deg, #f7f7f7 40%, transparent 75%);
  }
  &__inner {
    display: flex;
    position: relative;
    z-index: 3;
  }
  &__map {
    position: absolute;
    // position: relative;
    right: 0;
    top: 0;
    bottom: 0;
    width: 60%;
    height: 100%;
    z-index: 1;
    background: no-repeat right center / auto 100%;
    background-color: #f7f7f7;
    background-image: url(../img/bg/map.jpg);
    // height: 100%;
  }
  &__column {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  &__title {
    font-size: $fz-title;
    font-weight: 800;
    text-transform: uppercase;
    margin-bottom: 80px;
  }
  &__row {
    font-size: $fz-subtitle;
    margin-bottom: 45px;
  }
  &__tel {
    font-size: $fz-title;
    font-weight: 800;
    text-transform: uppercase;
    &:hover {
      text-decoration: underline;
    }
  }
  &__btn {}
}

@media (min-width: 1400px) {}
@media (min-width: 992px) and (max-width: 1199px) {}
@media (min-width: 768px) and (max-width: 991px) {}
@media (max-width: 767px) {
  .contacts {
    // background-image: 
    //   linear-gradient(180.0deg, #f7f7f7 35%, transparent 100%),
    //   url(../img/bg/map.jpg);
    // background-position: center top, center bottom;
    // background-color: #f7f7f7;
    padding: 25px 0 220px;
    &::before {
      background: no-repeat center top / cover;
      background-image: 
        linear-gradient(180.0deg, #f7f7f7 40%, transparent 75%);
    }
    &__inner {
      padding: 0 20px;
      flex-direction: column;
      align-items: center;
    }
    &__map {
      top: auto;
      left: 0;
      width: 100%;
      height: 50%;
      background-position: bottom center;
      background-size: 100% auto;
    }
    &__column {
      // margin: 0 auto;
    }
    &__title {
      font-size: $fz-title-mob;
      margin-bottom: 30px;
    }
    &__btn {
      font-size: $fz-title-mob;
      padding: 20px 45px;
    }
    &__row {
      font-size: $fz-title-mob;
      margin-bottom: 10px;
      &:nth-of-type(even) {
        margin-bottom: 30px;
      }
    }
    &__tel {
      font-size: $fz-default;
    }
  }
}
@media (max-width: 480px) {
  .contacts {
    background-image: 
      linear-gradient(180.0deg, #f7f7f7 50%, transparent 100%),
      url(../img/bg/map.jpg);
    &__map {}
  }
}
@media (max-width: 320px) {}