.guarantee {
  // $brs-inflated: 50% 20px;
  background: url(../img/bg/guarantee.jpg) no-repeat right bottom / auto 85%;
  padding: 95px 0 25px;
  &__inner {}
  &__row {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
  }
  &__header {
    margin-bottom: 5px;
    & br {
      display: none;
    }
  }
  &__item {
    position: relative;
    &:nth-of-type(2) {
      margin-top: 185px;
      margin-left: 40px;
    }
    &--mob {
      display: none;
    }
  }
  &__text-mob {
    margin: 0 auto;
    font-size: $fz-subtitle-mob;
    line-height: 16px;
    text-align: center;
  }
  &__glove {
    width: 410px;
    height: calc(410px * 1.48);
    background: no-repeat left bottom / contain;
    background-clip: border-box;
    background-image: url(../img/content/glove.png);

    position: relative;
    z-index: 2;
    // & > .guarantee__card {

    // }
    &::after {
      // content: url(../img/content/finger.png);
      content: '';
      background: no-repeat left bottom / contain;
      background-image: url(../img/content/finger.png);
      display: block;
      position: absolute;
      bottom: 0;
      left: 0;
      width: 51%;
      height: 100%;
    }
  }
  
  &__card {
    background-color: #fff;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    padding: 35px 25px;
    width: 290px;
    border: 2px solid #fc9e49;
    @include card-inflated-top(20px);

    &--in-glove {
      position: relative;
      top: 75px;
      left: 140px;
    }
  }
  &__card-title {
    font-size: 3.125rem;
    font-weight: 800;
    text-transform: uppercase;
    margin-bottom: 15px;
  }
  &__card-text {
    line-height: 30px;
  }
  &__img {
    position: relative;
    right: 20px;
    top: 50px;
  }
  &__img-text {
    font-style: oblique;
    font-size: 0.625rem;
    line-height: 12px;
    display: none;
    &::after {
      content: '';
      display: block;
      width: 30px;
      height: 10px;
      background: no-repeat center / cover;
      background-image: url(../img/icon/arrows/short.png);
      transform: rotateX(180deg) rotateZ(-35deg);
      margin-left: 35%;
      margin-top: 10px;
    }
  }
}

@media (min-width: 1200px) {}
@media (max-width: 1100px) {
  .guarantee {
    &__header br {
      display: inline;
    }
  }
}
@media (min-width: 992px) and (max-width: 1199px) {
  .guarantee {
    &__item {
      &:nth-of-type(2) {
        margin-left: -2%;
        margin-top: 26%;
      }
    }
  }
}
@media (min-width: 900px) and (max-width: 991px) {
  .guarantee {
    &__item {
      &:nth-of-type(2) {
        margin-left: -5%;
        margin-top: 26%;
      }
    }
    &__card {
      padding: 25px 10px 20px 10px;
      width: 250px;
    }
  }
}
@media (min-width: 768px) and (max-width: 899px) {
  .guarantee {
    &__row {
      flex-direction: column;
    }
    &__item {
      &--mob {
        display: block;
        text-align: center;
        padding: 0 30px;
        margin: 0 auto;
      }
      &:nth-of-type(2) {
        margin-left: 380px;
        margin-top: -30%;
        margin-bottom: 60px;
      }
      &--contract {
        margin: 0 auto;
      }
    }
    &__text-mob {
      font-size: $fz-subtitle;
    }
    & .section-header__subtitle {
      display: none;
    }
  }
}
@media (max-width: 767px) {
  .guarantee {
    // background-size: 180% auto;
    background-size: auto 43%;
    padding: 40px 0;
    & .section-header__subtitle {
      display: none;
    }
    &__row {
      flex-direction: column;
      align-items: center;
    }
    &__item {
      &--mob {
        display: block;
      }
      &--glove {
        // margin-left: -150px;
      }
      &:nth-of-type(2) {
        margin-left: 240px;
        margin-top: -145px;
        margin-bottom: 60px;
      }
    }
    &__glove {
      margin-left: -180px;
      width: 250px;
      height: calc(250px * 1.48);
      & > .guarantee__card {
      }
    }
    &__card {
      padding: 15px 20px 20px 20px;
      width: 200px;
      @include card-inflated-top(8px);
      &--in-glove {
        top: 50px;
        left: 90px;
      }
    }
    &__card-title {
      font-size: 1.750rem;
      margin-bottom: 15px;
    }
    &__card-text {
      font-size: $fz-default;
      line-height: 1.2;
    }
    &__text-mob {
      font-size: $fz-base;
      line-height: 1.2;
      margin-bottom: 25px;
    }
    &__img {
      margin: 0 auto;
      max-width: 50%;
      position: static;
    }
    &__img-text {
      display: block;
      position: absolute;
      left: 0;
      top: 25%;
      max-width: 100px;
    }
  }
}
@media (max-width: 480px) {
  .guarantee {
    &__item {
      &:nth-of-type(2) {
        margin-left: 140px;
        margin-top: -100px;
        margin-bottom: 60px;
      }
    }
    &__glove {
      width: 160px;
      margin-left: -150px;
      height: calc(160px * 1.48);
    }
    &__card {
      padding: 15px 5px 20px 15px;
      width: 160px;
      &--in-glove {
        top: 15px;
        left: 60px;
      }
    }
    &__text-mob {
      font-size: $fz-subtitle-mob;
      margin-bottom: 15px;
    }
    &__card-title {
      font-size: $fz-subtitle;
      margin-bottom: 0;
    }
    &__card-text {
      font-size: $fz-default-mob;
    }
  }
}
@media (max-width: 320px) {}